import React from "react"

function LightModeChangeHandler(){
    localStorage.setItem("DarkMode", "False")
    console.log("setting darkmode to", localStorage.getItem("DarkMode"))
    document.body.classList.remove("darkmode")
    if (document.getElementById("searchform")){
        document.getElementById("searchform").classList.remove('darkjumbotron')
    }
    if (document.getElementById("moviedetail")){
        document.getElementById("moviedetail").classList.remove('darkmoviedetail')
}
}
  
function DarkModeChangeHandler(){
    localStorage.setItem("DarkMode", "True")
    console.log("setting darkmode to",localStorage.getItem("DarkMode"))
    document.body.classList.add("darkmode")
    if (document.getElementById("searchform")){
        document.getElementById("searchform").classList.add('darkjumbotron')
    }
    if (document.getElementById("moviedetail")){
        document.getElementById("moviedetail").classList.add('darkmoviedetail')
}
}

function darkmodetoggle(){
    if(localStorage.getItem("DarkMode") === "True"){
        LightModeChangeHandler()
    }else{
        DarkModeChangeHandler()
    }
}



function Darkmode(){
    return (
        <div>
           <label for="darkmodebox">Dark Mode<input id="darkmodebox" type="checkbox" onChange={() => darkmodetoggle()} /></label>
        </div>
    )
}

export default Darkmode